import {addressList} from '@/api/user.js'
import navT from '../../components/nav/nav.vue'
export default {
	components:{
		navT
	},
	data() {
		return {
			list:[]
		}
	},
	methods: {
		toAddAddress(){
			uni.navigateTo({
				url:'/pages/addAddress/addAddress'
			})
		},
		toUpdateAddress(id){
			uni.navigateTo({
				url:'/pages/updateAddress/updateAddress?id='+id
			})
		}
	},
	onShow() {
		
		addressList().then(data=>{
			this.list=data.data.data
		})
	}
	
}
